import axios from 'axios'
import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { baseUrl } from '../baseUrl'
import './Login.css'

export default function Register() {
    const [user, setUser] = useState({
        email: '',
        password: '',
        confirmPassword: ''
    })

    const onSubmitForm = e => {
        e.preventDefault()
        axios.post(`${baseUrl}/auth/register`,{
            email: user.email,
            password: user.password
        })
        // .then(res => ))
    }

  return (
    <div className='register align-items-center' style={{background: 'url(/img/bg.jpg)', backgroundSize: 'cover'}}>
        <div className='register-form'>
            <h1 className='mt-4 text-light'>Register</h1>
            <form className='m-4 d-flex flex-column' onSubmit={onSubmitForm} method='POST'>
                <input
                    name='email'
                    value={user.email}
                    type='email'
                    placeholder='Email'
                    onChange={(e) => setUser(v => {
                        return {...v, email: e.target.value}
                    })}
                />
                <input
                    name='password'
                    value={user.password}
                    type='password'
                    placeholder='Password'
                    onChange={(e) => setUser(v => {
                        return {...v, password: e.target.value}
                    })}
                />
                <input
                    name='confirmPassword'
                    value={user.confirmPassword}
                    type='password'
                    placeholder='Confirm password'
                    onChange={(e) => setUser(v => {
                        return {...v, confirmPassword: e.target.value}
                    })}
                />
                {/* <div className='bg-danger p-2' style={{display: message.display}}>
                    <span className='text-light'>
                        Email or password incorrect!
                    </span>
                </div> */}
                <button className='btn btn-success text-light mt-4' type='submit' >Register</button>
                <p className='text-light m-1'>or</p>
                <Link className='text-light text-decoration-none' to='/login'>
                    <button className='btn btn-info' style={{width: '100%'}}>Login</button>
                </Link>
                
            </form>
        </div>
    </div>
  )
}
