import React, { useState, useContext } from 'react'
import { useNavigate, Link } from 'react-router-dom'
import './Login.css'
import { baseUrl } from '../baseUrl'
import axios from 'axios'
import { AuthContext } from '../../contexts/AuthContext'

export default function Login() {
    const Auth = useContext(AuthContext)
    const navigate = useNavigate()
    const [user, setUser] = useState({
        email: '',
        password: ''
    })

    const [message, setMessage] = useState({
        display: 'none'
    })

    const onSubmitForm = e => {
        e.preventDefault()
        axios.post(`${baseUrl}/auth/login`,{
            email: user.email,
            password: user.password
        })
        .then(res => {
            if(res.data.success){
                Auth.dispatch({type: "LOGIN", payload: res.data.user.email})
                localStorage.setItem('token', res.data.token)
                localStorage.setItem('userId', res.data.user._id)
                navigate('/')
            } else {
                setMessage({display: 'block'})
            }
        })
        .catch(err => console.log(err))
    }

    
  return (
    <div className='login align-items-center' style={{background: 'url(/img/bg.jpg)', backgroundSize: 'cover'}}>
        <div className='login-form'>
            <h1 className='mt-4 text-light'>Login</h1>
            <form className='m-4 d-flex flex-column' onSubmit={onSubmitForm} method='POST'>
                <input
                    name='email'
                    value={user.email}
                    type='email'
                    placeholder='Email'
                    onChange={(e) => setUser(v => {
                        return {...v, email: e.target.value}
                    })}
                />
                <input
                    name='password'
                    value={user.password}
                    type='password'
                    placeholder='Password'
                    onChange={(e) => setUser(v => {
                        return {...v, password: e.target.value}
                    })}
                />
                <div className='bg-danger p-2' style={{display: message.display}}>
                    <span className='text-light'>
                        Email or password incorrect!
                    </span>
                </div>
                <button className='btn btn-info text-light mt-4' type='submit' >Login</button>
                <p className='text-light m-1'>or</p>
                <Link className='btn text-light text-decoration-none p-0' to='/register'>
                    <button className='btn btn-success' style={{width: '100%'}}>Register</button>
                </Link>
            </form>
        </div>
    </div>
  )
}
