import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { baseUrl } from '../baseUrl'
import './Dashboard.css'
import { Modal } from 'react-bootstrap'

export default function Dashboard() {
  const [userId, setUserId] = useState(() => {
    const id = localStorage.getItem('userId')
    return id
  })
  const [postItem, setPostItem] = useState([])
  const [postInput, setPostInput] = useState({
    title: '',
    description: ''
  })
  const onSubmitForm = e => {
    e.preventDefault()
    axios.post(baseUrl+'/post/create',{
      title: postInput.title,
      description: postInput.description,
      userId: userId
    })
    .then(res => setPostItem(v => {
      return [...v, res.data.data]
    }))
    .catch(err => console.log(err))
  }

  useEffect(() => {
    axios.get(baseUrl+'/post',{
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token')
      }
    })
      .then(res => {
        res.data.map(data => setPostItem(v => [...v,data]))
      })
      .catch(err => console.log(err))
    // return setPostItem([])
  },[])


  // Modal
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

  // Delete item
    const delItem = (i,id) => {
      axios.delete(baseUrl+'/post/delete/'+id)
      postItem.splice(i,1)
      setPostItem([...postItem]) 
    }

  return (
    <div className='dashboard container'>
        <ul className='post-lists d-flex mt-4'>
            {
            postItem.length<1 ? <h1 style={{flex: '1'}}>No item available</h1>
            :
            postItem.map((v,i) => {
             return ( 
                <li key={i} className='post-item mb-2'>
                    {/* <Link to={`/dashboard/item?id=${v._id}`} className='text-dark text-decoration-none'> */}
                    <h4>{v.title}</h4>
                    <p className='m-0'>{v.description}</p>
                    {/* </Link> */}
                    <button className='post-item__btn edit btn-info p-2 text-light'>Edit</button>
                    <button 
                      className='post-item__btn del btn-danger p-2'
                      onClick={() => delItem(i,v._id)}
                    >
                      Del
                    </button>
                </li>
            )
            })
            }
        </ul>
        <button className='modal-btn' variant="primary" onClick={handleShow} style={{borderRadius: '50%'}}>
          +
        </button>

        <Modal
          show={show}
          onHide={handleClose}
          backdrop="static"
          keyboard={false}
        >
          <Modal.Header closeButton>
            <Modal.Title>Create your text</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <form className='dashboard-form' onSubmit={onSubmitForm}>
              <input
                className='col-12 mb-2'
                placeholder='Title'  
                value={postInput.title}
                onChange={e => setPostInput(v => {
                  return {...v, title: e.target.value}
                })}
              />
              {/* <input
                className='col-12 mb-2'
                type='textaria'
                placeholder='Description' 
                value={post.description}
                onChange={e => setPost(v => {
                  return {...v, description: e.target.value}
                })}
              /> */}
              <textarea
                className='col-12 mb-2'
                type='textaria'
                placeholder='Description' 
                value={postInput.description}
                onChange={e => setPostInput(v => {
                  return {...v, description: e.target.value}
                })}
              ></textarea>
              <button type='submit' className='col-12 btn btn-success' onClick={handleClose}>Create</button>
            </form> 
          </Modal.Body>
          {/* <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary">Understood</Button>
          </Modal.Footer> */}
        </Modal>

    </div>
  )
}
