import React, {useContext,useState, useEffect,useRef} from 'react'
import { Link } from 'react-router-dom'
import { Container, Navbar, Nav , NavDropdown } from 'react-bootstrap'
import { AuthContext } from '../../contexts/AuthContext'
import {baseUrl} from '../baseUrl'
import {io} from 'socket.io-client'
import './Home.css'
import axios from 'axios'

const socket = io.connect('https://nhut321-server.herokuapp.com/')

export default function Home() {
  const [date, setDate] = useState({
    hour: 0,
    minute: 0
  })
  const [message, setMessage] = useState('')
  const [messageItem, setMessageItem] = useState(() => {
    // return JSON.parse(localStorage.getItem('message')) ?? []
    return []
  })
  const Auth = useContext(AuthContext)

  const inputMess = useRef()
  const chatDiv = useRef()

  // useEffect(() => {
  //   axios.post(baseUrl + '/chat/create', {
  //     message,
  //     name: Auth.state.user,
  //     headers: {
  //       Authorization: 'Bearer ' + localStorage.getItem('token')
  //     }
  //   }).then(res => console.log(res.data))
  // },[])

  useEffect(() => {
    axios.get(`${baseUrl}/chat`,{
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token')
      }
    })
      .then(res => {
          res.data.map(v => {
            setMessageItem(item => {
              return [...item, {
                message: v.message,
                name: v.name.split('@')[0]
              }]
            })
        })
      })
  },[])

  useEffect(() => {
      chatDiv.current.scrollTop = chatDiv.current.scrollHeight
    return function cleanup() {
      
    }

  },[messageItem])
  

  useEffect(() => {
    socket.on('message', ({ name, message }) => {
      setMessageItem(item => {
        const result = [...messageItem, { name, message }]
        // localStorage.setItem('message', JSON.stringify(result))
        return result
      })
    })
    // chatDiv.current.scrollTop = chatDiv.current.scrollHeight
  })

  const submitFormMessage = e => {
    e.preventDefault()
    if(!message == '') {
      socket.emit('message', {name: Auth.state.user.split('@')[0], message})
      setMessage('')
      inputMess.current.focus()
      const date = new Date()
      setDate({hour: date.getHours(), minute: date.getMinutes()})
      axios.post(baseUrl + '/chat/create', {
        message,
        name: Auth.state.user,
        time: {
          hour: date.hour,
          minute: date.minute
        },
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }).then(res => console.log(res.data))
    }
  }


  return (
    <>
      <div className='home mt-4 container'>
          <h3>Welcome to {Auth.state.user}!</h3>
          <Link to='/dashboard' className='m-4'>
            <button className='btn btn-info text-light'>Go to Dashboard</button>
          </Link>
          <div ref={chatDiv} className="chat">
            <div className="chat-history">
                <ul className="m-b-0">

                    { messageItem.map((v,i) => {
                      return (
                        <li key={i} className={v.name === Auth.state.user.split('@')[0] ? "chat-history-item me clearfix" : "chat-history-item friend clearfix" }>
                          <div className="name mb-4 d-flex align-items-center">
                            <h5>{v.name}</h5>
                            <img src="/img/avatar.png" alt="avatar"/>
                          </div>
                          <div className={v.name === Auth.state.user.split('@')[0] ? "message my-message float-right" : "message other-message float-right"}>{v.message}</div>
                          <div className="message-data text-right">
                              <span className="message-data-time">{(date.hour < 9 ? '0' + date.hour : date.hour) + ':' + (date.minute < 9 ? '0' + date.minute : date.minute)} { date.hour > 12 ? 'PM' : 'AM'}</span>
                          </div>
                        </li>
                      )
                    })}

                    {/* <li className="chat-history-item friend clearfix"> */}
                    {/*     <div className="message-data"> */}
                    {/*         <span className="message-data-time">10:12 AM, Today</span> */}
                    {/*     </div> */}
                    {/*     <div className="message my-message">Are we meeting today?</div>                                     */}
                    {/* </li>                                */}
                    {/* <li className="chat-history-item friend clearfix"> */}
                    {/*     <div className="message-data"> */}
                    {/*         <span className="message-data-time">10:15 AM, Today</span> */}
                    {/*     </div> */}
                    {/*     <div className="message my-message">Project has been already finished and I have results to show you.</div> */}
                    {/* </li> */}
                    {/* <li className="chat-history-item friend clearfix mb-4"> */}
                    {/*     <div className="message-data"> */}
                    {/*         <span className="message-data-time">10:15 AM, Today</span> */}
                    {/*     </div> */}
                    {/*     <div className="message my-message">Project has been already finished and I have results to show you.</div> */}
                    {/* </li> */}

                </ul>
              </div>
          </div>
      </div>
      <div className="chat-message clearfix container">
        <form className="input-group mb-0" onSubmit={submitFormMessage}>
            <input
              ref={inputMess}
              type="text" className="form-control" 
              placeholder="Enter text here..."
              value={message}
              onChange={e => setMessage(e.target.value)}
            />
            <button className='btn btn-info text-light'>Send</button>   
        </form>
      </div>

    </>
  )
}
